@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
position: absolute;
width: 1440px;
left: 0px;
top: 0px;
}

body {
  background: #1F1F1F;
  color: #FFF;
  font: 400 14px Raleway, sans-serif;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
}

.fonte-roxa{
  color: #9045F0;
}

.linha-horizontal{
  color: #fff;
  top: 99vh;
  width: 100vw;
  border: 1px solid #fff;
  position: absolute;
}
.fundo-galeria{
  width: 100%;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}

.conteudo-galeria{
  width: 100%;
  height: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
}

.conteudo-galeria .btnFecharModal {
  width: 100%;
  height: 25px;
}

.conteudo-galeria span  {
  cursor: pointer;
  float: right;
}

@media only screen and (min-width: 768px) {
  .conteudo-galeria{
    width: 50%;
    height: 50%;
  }
}
.projetos{
  width: 90%;
  margin-bottom: 20%;
}

.projetos h2{
  font-size: 1em !important;
}

.projetos ul{
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 18px;
  list-style: none;
  width: 100%;
}

.projetos ul li img{
  background-color: #fff;
  width: 100%;
  height: 200px;
  border-radius: 10px;
  object-fit: contain;
}

.projetos ul li img{
  width: 100%;
  /* height: 300px; */
  border-radius: 10px;
}

.conteudo-galeria span{
  margin-left: auto;
  right: 0;
}


.projetos ul li {
  cursor: pointer;
}

@media only screen and (min-width: 768px) {
  .projetos ul{
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (min-width: 1225px) {
  .projetos ul .logo-safishare img{
    padding: 50px;
    padding-top: 0;
  }
}


.container{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.container section{
  height: 100%;
  /* border: 1px solid #fff; */
}

.container section.menu{
  width: 35%;
  position: fixed;
  top: 0;
  left: 0;
  margin-top: 15%;
}

.container section.paginas{
  width: 65%;
  position: fixed;
  top: 0;
  left: 35%;
  overflow-y: auto;
  height: 90%;
  overflow-x: hidden;
  /* background-color: red; */
}

.barra-lateral{
  position: fixed;
  width: 0px;
  height: 70%;
  top: 16%;
  border: 2px solid #9045F0;
}

.conteudo{
  /* background-color: blue; */
  color: #fff;
  width: 70%;
  height: 50%;
  position: relative;
  font-size: 2em;
  text-align: left;
  margin: 80% auto;
}

.conteudo h2{
  margin-top: 0;
  text-align: center;
  /* display: none; */
  color: #9045F0;
  margin-bottom: 1em;
  font-size: 2em;
}

@media only screen and (max-width: 320px) {
  .container section.menu{
    background-color: yellow;
  }
}

@media only screen and (min-width: 721px) {
  .conteudo{
    margin: 60% auto;
    font-size: 4em;
  }
}

@media only screen and (min-width: 768px) {
  .conteudo{
    margin: 20% auto;
  }

  .container section.menu{
    max-width: 250px;
    margin-top: 5%;
    margin-left: 5%;
  }
}

@media only screen and (min-width: 1225px) {
  .container section.paginas{
    width: 80%;
    left: 25%;
  }
}

.imgPerfil{
  display: flex;
  justify-content: center;
  margin: auto;
}

.menu-lateral .imgPerfil img{
  border-radius: 50%;
  width: 96px;
  height: 96px;
}

.titulo-perfil{
  font-size: 1em;
  line-height: 1.2em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.titulo-perfil span.nome{
  font-size: 2em;
  line-height: 1.2em;
}

.menu-lateral .icones{
  width: 100%;
  /* background-color: red; */
  margin-top: 30%;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
}

.menu-lateral .icones ul{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  line-height: 48px;
  list-style: none;
  margin-top: 15%;
  width: 100%;
  text-align: center;
}

.menu-lateral nav{
  width: 80%;
  margin-left: 10%;
  position: absolute;
  bottom: 15%;
  /* margin-top: 40%; */
}

.menu-lateral nav ul li{
  display: inline;
}

.menu-lateral nav ul li a{
  color: #FFF;
  text-decoration: none;
  margin-left: 5%;
}

.menu-lateral nav ul li a span {
  display: none;
}

.menu-lateral nav ul li a:hover,
.menu-lateral nav ul li a .icon:hover{
  color: #9045F0 !important;
}

@media only screen and (max-width: 320px) {
  .menu-lateral .icones ul{
    grid-template-columns: repeat(3, 1fr);
  }

  .menu-lateral .icones ul li .icon{
    width: 18px;
    height: 18px;
  }

  .menu-lateral nav ul li .icon{
    width: 18px;
    height: 18px;
  }
  
}

@media only screen and (min-width: 721px) {
  .menu-lateral nav{
    margin-left: 0;
  }

  .menu-lateral .icones ul{
    grid-template-columns: repeat(3, 1fr);
  }

  .menu-lateral .icones ul li .icon{
    width: 60px;
    height: 60px;
  }
}

@media only screen and (min-width: 768px) {
  
  .titulo-perfil{
    font-size: 2em;
    line-height: 1.2em;
  }

  .menu-lateral .icones ul li .icon{
    width: 32px;
    height: 32px;
  }

  .menu-lateral nav ul li .icon{
    width: 32px;
    height: 32px;
  }

  .menu-lateral nav{
    width: 90%;
    margin-left: 5%;
  }

  .menu-lateral nav ul li a span {
    display: inline-flex;
    font-size: 2em;
    width: 70%;
  } 
}

@media (max-height: 680px){
  .menu-lateral nav ul li a{
    color: #FFF;
    text-decoration: none;
    margin-left: 5%;
  }
  
  .menu-lateral nav ul li a span {
    display: none;
  }
}

@media(max-height: 580px){
  .menu-lateral .icones{
    margin-top: 0;
  }
}


.sobre-mim{
  position: relative;
  width:100%;
  height:50vh;
}

.txt-sobre{
  /* margin-top: 60vh; */
  top: 0;
  font-size: 1.2em;
  width: 80%;
}

@media only screen and (min-width: 768px) {
  .txt-sobre{
    font-size: 2em;
  }
}

